@import "../../stylesheets/variables";

.header__container {
  background-color: #5A2A2F;
  color: white;
  height: 100px;
}

.header__logo-title{
  font-size: 30px;
  font-weight: normal;
  color:#ED553B;
}

.header__wrapper {
  display: flex;
  flex-direction: column;
  padding: 10px;
  justify-content: center;
  align-items: center;
}
.menu__list {
  font-size: 24px;
  width: 250px;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}
.link__header {
  cursor: pointer;
  transition: color ease 0.5s;
  &:hover {
    color: #ED553B;
  }
}

